body{
    background-image: -webkit-gradient(linear,left top, right bottom,from(black), color-stop(gray),to(black));
    background-image: -webkit-linear-gradient(top left,black, gray,black);
    background-image: -o-linear-gradient(top left,black, gray,black);
    background-image: linear-gradient(to bottom right,black, gray,black);
}
.logo{
    height: 2em;
}
.navbar{
    background-color: #6ea328;
}
.icon{
    font-size: 55px;
    color: rgba(0, 0, 0, 0.651);
}
.box{
    height: 10%;
}
.inputxt{
    width: 90%;
}
.imgu{

    margin-top: 1em;

}
@media (min-width: 1000px) {
    .textIcon{
        display: none;
    }
}

.divPost{
    opacity: 0.5;
    padding: 1em;
    height: 7em;
    word-break: break-all;
    word-wrap: break-word;
}
.inpt{
    width: 100%;
}


